.entry-page {
  width: 100%;
  height: 100%;
}

.back-img {
  background-image: url(./assets/bg4.jpg);
  background-position: -30% center;
  background-repeat: no-repeat;
  background-size: 70%;
  background-color: rgb(11, 11, 39);
