#login,
#signUp,
#PWReset {
  width: 35%;
  padding: 4%;
  height: 68%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 7%;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 2px 0 15px rgba(0, 0, 0, 0.25);
  opacity: 1;

  /* background-color: skyblue; */
}

.login-title {
  font-size: 24px;
  /* font-weight: 700; */
  margin-top: 1%;
  color: #04BEFE;
}

.username,
.password {
  margin-top: 10%;
  border-bottom: 1px solid #f0f0f0;
}

.register-input {
  margin-top: 5%;
  border-bottom: 1px solid #f0f0f0;

  &:hover {
    border-color: #ccc;
  }

}

.register-input .ant-form-item-required {
  color: #bbb;
}

.username:hover,
.password:hover {
  border-color: #ccc;
}

.submit {
  text-align: center;
  margin-top: 15%;
}

.login-form-button {
  width: 100%;
  color: #fff;
  border-width: 0;
  /* height: 36px; */

  >span {
    position: relative;
  }

  &::before {
    content: '';
    background: linear-gradient(135deg, #6253E1, #04BEFE);
    position: absolute;
    inset: 0;
    opacity: 1;
    transition: all 0.3s;
    border-radius: inherit;
  }

  &:hover::before {
    opacity: 0;
  }
}



.reset-password {
  text-align: right;
  display: inline-block;
  color: #ccc;
  cursor: pointer;

  &:hover {
    color: #aaa;
  }
}

.register {
  text-align: right;
  margin-top: 16px;
  display: inline-block;
  color: #ccc;
  cursor: pointer;

  &:hover {
    color: #aaa;
  }
}

.ant-checkbox-wrapper {
  color: #ccc;
}

.remember {
  color: #ccc;

  &:hover {
    color: #aaa;
  }
}

.navigation {
  width: 100%;
  height: 100%;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: absolute;
  right: 10px;
  top: 20px;
  z-index: 10;
  cursor: pointer;
  /* height: 68px; */
  /* background-color: #04BEFE; */
  /* box-shadow: 2px 0 15px rgba(0, 0, 0, 0.25); */
}

.navigation {
  background: #222;
  margin: 0rem;
  min-height: 100vh;
  font-family: Futura, sans-serif;
}


.banner {
  /* height: 300px; */
  height: 100%;
  width: 100%;
}

#canvas,
#text {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  z-index: 1;
}

#text {
  box-shadow: 2px 0 15px rgba(0, 0, 0, 0.25);
  min-height: 30%;
  width: 100%;
  z-index: 2;
  color: #fff;
  text-transform: uppercase;
  font-size: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

#text h1 {
  opacity: 0.9
}

.content {
  position: absolute;
  top: 32%;
  background-color: #333;
  z-index: 9;
  width: 80%;
  height: 66%;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 8px;
  box-shadow: 2px 0 15px rgba(0, 0, 0, 0.25);
  display: flex;
  align-items: center;
  justify-content: center;
}

.prev,.next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 50px;
  z-index: 99;
  color: #444;
  cursor: pointer;
  &:hover {
    color: #f0f0f0;
  }
}
.prev {
  left: 20px;
}
.next {
  right: 20px;
}

